import { Link, NavLink } from "react-router-dom";
import logo from '../images/logo.svg';
import { useState } from "react";

// const toggleHandler = (_event: any) => {

// };

const Header = () => {
    const [isOn, setisOn] = useState(false);

    const toggleHandler = () => {
      // isOn    
      setisOn(!isOn)
    };

    const handleLinkClick = () => {
      //     (:  )
      console.log('Link clicked!');
    };
  

    return (
      <>
        <header className={`${isOn ? "is-active" : ""}`}>
          <div className="pc-header-wrap">
            <div className="logo-wrap">
            <Link to='/'><img alt=" " src={logo} /></Link>
            </div>
            <div className="text-wrap">
                <div className="header-link-wrap">
                  <NavLink to='/blueberry' className={({ isActive }) => (isActive ? "active" : "")}> </NavLink>
                  <NavLink to='/caring' className={({ isActive }) => (isActive ? "active" : "")}>  </NavLink>
                  <NavLink to='/shop' className={({ isActive }) => (isActive ? "active" : "")}> </NavLink>
                </div>
            </div>
          </div>
          <div className="mw-header-wrap hide">
            <div className="logo-wrap">
            <Link to='/'><img alt=" " src={logo} /></Link>
            </div>
            <div className="burger-sticks" onClick={toggleHandler}>
              <span></span>
              <span></span>
              <span></span>
            </div>
            <div className="text-wrap hide">
                <div className="header-link-wrap">
                  <NavLink to='/blueberry' onClick={handleLinkClick} className={({ isActive }) => (isActive ? "active" : "")}> </NavLink>
                  <NavLink to='/caring' onClick={handleLinkClick} className={({ isActive }) => (isActive ? "active" : "")}>  </NavLink>
                  <NavLink to='/shop' onClick={handleLinkClick} className={({ isActive }) => (isActive ? "active" : "")}> </NavLink>
                </div>
            </div>
          </div>
        </header>
      </>
    );
  };

export default Header;