import ProductList from './components/ProductList';
import { useNavigate } from 'react-router-dom';
import Meta from './components/Meta';

const Main = () => {
  const ProductListProps = {
    limit: 3,
    currentPage: 1,
    type: "FIX"
  };

  const ProductListSwiperProps = {
    limit: 10,
    currentPage: 1,
    type: "SWIPER"
  };
  
  const navigate = useNavigate(); // navigate    

  const goBlueberryPage = (_event: any) => {
    navigate("/blueberry");
  };
  
  const goCaringPage = (_event: any) => {
    navigate("/caring");
  };
  
  const goShopPage = (_event: any) => {
    _event.preventDefault();
    navigate("/shop");
  };

  return (
    <>
      <Meta title=" |      " description="      ." />
        <main className="home">
        <div className="top-wrap">
          <div className="main-text-wrap">
            <div>
              <p className="title"> <br className="mw-tag hide" />    , </p>
              <p>   ,<br className="mw-tag hide" />     <br className="mw-tag hide" />   .</p>
              <button onClick={goBlueberryPage}> </button>
            </div>
          </div>
        </div>
        <div className="about-wrap">
          <div className="contents">
            <div>
              <p className="green-color-title">About us</p>
              <p> <br className="mw-tag hide" />     <br className="mw-tag hide" />  .</p>
            </div>
            <div className="box-wrap">
              <div onClick={goBlueberryPage} className="blueberry-wrap">
                <p className="title"> </p>
                <p><button> </button></p>
              </div>
              <div onClick={goCaringPage} className="caring-wrap">
                <p className="title">  </p>
                <p><button> </button></p>
              </div>
            </div>
          </div>
        </div>
        <div className="product-wrap">
          <div>
            <p className="sub-title"> </p>
            <p>     <br className="mw-tag hide" />  </p>
            <button onClick={goShopPage}> </button>
          </div>
          <div className="product-wrap-list pc-tag hide"><ProductList {...ProductListProps}/></div>
          <div className="product-wrap-list mw-tag hide"><ProductList {...ProductListSwiperProps}/></div>
        </div>
      </main>
    </>
  )
}
  
export default Main